*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body{
    display: flex;
    flex-direction: column;
    width: 100%;
    min-height: 100vh;

    > *{
        width: 100%;
    }
}

header{
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding-inline: 48px;
    background-color: #fffef0;

    > figure {
        width: 100px;

        > img{
            width: 100%;
        }

        > h1{
            color: #32d35a; 
        }
    }
}